.container {
    min-width: 500px;
    width: 100%;
}

.card-header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.divider{
    margin-top: 10px;
    margin-bottom: 20px;
}

.form-filed {
    width: 80%;
    margin: auto;
}

.spacer{
    margin-left: 20px;
}

.container-bottom {
    width: 90%;
    margin-right: 15px;
    margin-bottom: 10px;
}

.inner-form {
    display: flex;
    flex-direction: row;
    width: 90%;
    text-align: left;
    font-size: 16px;
    padding-left: 10px;
}

.form-row {
    margin-top: 18px;
}

.item-left {
    width: 35%;
    margin-right: 10px;
}

.item-right {
    width: 100%;
}